// var _ = require('underscore')

angular.module('well', [
  'ngRoute',
  'well.controllers',
  'well.filters',
  'well.services',
  'well.directives',
  'well.survey',
  'well.tradeoff',
  'well.overlap',
  'well.results'
])
.config(function ($routeProvider, $locationProvider) {
  $routeProvider
    .when('/', {
      templateUrl: 'landing.html'
    })
    .when('/auth', {
      templateUrl: 'auth.html'
    })
    .when('/consent', {
      templateUrl: 'consent.html'
    })
    .when('/terms', {
      templateUrl: 'terms.html'
    })
    .when('/privacy', {
      templateUrl: 'privacy.html'
    })
    // ThreeTwo Module
    .when('/threetwo/survey', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/threetwo/abbcca', {
      templateUrl: 'tradeoff.html',
      controller: 'ThreeCtrl'
    })
    .when('/threetwo/dbec', {
      templateUrl: 'tradeoff.html',
      controller: 'TwoCtrl'
    })
    .when('/threetwo/exit', {
      templateUrl: 'exit.html',
      controller: 'ExitCtrl'
    })
    // Overlap Module
    .when('/overlap/survey', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/overlap/abac', {
      templateUrl: 'tradeoff.html',
      controller: 'AbacCtrl'
    })
    .when('/overlap/abc', {
      templateUrl: 'overlap.html',
      controller: 'AbcCtrl'
    })
    .when('/overlap/exit', {
      templateUrl: 'exit.html',
      controller: 'ExitCtrl'
    })
    // Fine Overlap Module
    .when('/fineoverlap/survey', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/fineoverlap/tradeoff', {
      templateUrl: 'fineoverlap.html',
      controller: 'FineCtrl'
    })
    .when('/fineoverlap/exit', {
      templateUrl: 'exit.html',
      controller: 'ExitCtrl'
    })
    // Misc
    .when('/chart', {
      templateUrl: 'chart.html',
      controller: 'ChartCtrl'
    })
    .when('/dummy', {
      templateUrl: 'dummy.html',
      controller: 'DummyCtrl'
    })
    .when('/test', {
      templateUrl: 'test.html',
      controller: 'TestCtrl'
    })
    .otherwise({
      redirectTo: '/'
    })

  // $locationProvider.html5Mode(true)
})
