angular.module('well.results', [])
  .directive('sicilian', function ($window, Aspects) {
    return {
      restrict: 'EA',
      scope: {
        responses: '=',
        weights: '='
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0])
          .append('svg')
          .attr('class', 'sicilian')
          .style('width', '100%')

        svg.style('height', svg.style('width'))

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.$watch('responses', function () {
          scope.render()
        }, true)

        scope.$watch('weights', function () {
          scope.render()
        }, true)

        var sort = null
        attrs.$observe('sort', function (s) {
          sort = s
        })

        var names = Aspects.names()
        //   weights = _.map(names, function (e, i) {
        //     return Math.random()
        //   }),
        //   sum = _.reduce(weights, function (a, b) {
        //     return a + b
        //   }),
        //   data = _.map(names, function (e, i) {
        //     return {
        //       name: e,
        //       weight: weights[i] / sum
        //     }
        //   })

        // data = _.sortBy(data, function (d) {
        //   return d.weight * -1
        // })


        scope.render = function () {
          var data = _.map(names, function (a) {
            return {
              name: a,
              weight: scope.weights[a]
            }
          })
          data = _.sortBy(data, function (d) {
            if (sort === 'level') {
              return scope.responses[d.name]
            } else {
              return d.weight * -1
            }
          })

          svg.selectAll('*').remove()

          var width = svg.style('width').replace('px','')

          var g = svg.selectAll()
              .data(data)
            .enter().append('g')
              .style('height', function (d) {
                return d.weight * width
              })

          g.append('rect')
              .attr('y', function (d) {
                return _.reduce(data, function (a, b) {
                  if (data.indexOf(b) >= data.indexOf(d)) {
                    return a
                  } else {
                    return a + b.weight * width
                  }
                }, 0)
              })
              .attr('width', function (d) {
                return width
              })
              .attr('height', function (d) {
                return d.weight * width
              })
              .style('fill', function (d) {
                return Aspects.getColor(d.name, 30)
              })

          g.append('rect')
              .attr('y', function (d) {
                return _.reduce(data, function (a, b) {
                  if (data.indexOf(b) >= data.indexOf(d)) {
                    return a
                  } else {
                    return a + b.weight * width
                  }
                }, 0)
              })
              .attr('width', function (d) {
                return (scope.responses[d.name] / 100) * width
              })
              .attr('height', function (d) {
                return d.weight * width
              })
              .style('fill', function (d) {
                return Aspects.getColor(d.name, 140)
              })

          g.append('text')
              .attr('class', 'weight')
              .attr('y', function (d) {
                return d.weight * width * 0.5 + _.reduce(data, function (a, b) {
                  if (data.indexOf(b) >= data.indexOf(d)) {
                    return a
                  } else {
                    return a + b.weight * width
                  }
                }, 0)
              })
              .attr('dy', '.5em')
              .text(function (d) {
                return d3.format('%')(d.weight)
              })
              
        }
      }
    }
  })
  .directive('chart', function ($window, User) {
    return {
      restrict: 'EA',
      scope: {
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0]).append('svg')

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          if (data) scope.render()
        })

        var data
        User.getData('threetwo').then(function (response) {
          data = _.map(response, function (datum) {
            var score = _.reduce(datum.aspects, function (memo, num) {
              return memo + datum.responses[num] * datum.weights[num]
            }, 0)
            return {
              score: score,
              date: datum.date
            }
          })

          if (data.length === 1) {
            var before = _.clone(data[0])
            before.date = data[0].date - 50000
            var after = _.clone(data[0])
            after.date = data[0].date + 50000
            data = [before, data[0], after]
          }
          console.log(data)
          scope.render()
        })

        scope.render = function () {
          console.log(data)

          svg.selectAll('*').remove()
          var bounds = element[0].parentNode.getBoundingClientRect()
          console.log(bounds)
          var height = bounds.width / 3
          var width = bounds.width

          svg
            .attr('width', width)
            .attr('height', height)

          var x = d3.time.scale()
              .domain( [data[0].date, data[data.length - 1].date] )
              .rangeRound([30, width - 30])

          var y = d3.scale.linear()
            .domain([0, 100])
            .rangeRound([height - 30, 30])

          var xAxis = d3.svg.axis()
            .scale(x)
            .orient('bottom')

          var yAxis = d3.svg.axis()
            .scale(y)
            .orient('left')

          svg.append('g')
            .attr('class', 'x axis')
            .attr('transform', 'translate(0, ' + Number(height - 30) + ')')
            .call(xAxis)

          svg.append('g')
            .attr('class', 'y axis')
            .attr('transform', 'translate(30,0)')
            .call(yAxis)

          var line = d3.svg.line()
            .x(function(d) { return x(d.date) })
            .y(function(d) { return y(d.score) })
            .interpolate('basis')

          svg.append('path')
            .attr('class', 'line')
            .datum(data)
            .attr('d', line)
        }
      }
    }
  })
  .controller('ExitCtrl', function ($scope, User, $location) {
    $scope.satisfaction = {
      level: null,
      comments: null,
      submitted: false,
      submit: () => {
        $scope.satisfaction.submitted = true
        User.addData('feedback', {
          t: Date.now(),
          module: $location.path().split('/')[1],
          level: $scope.satisfaction.level,
          comments: $scope.satisfaction.comments
        })
      }
    }
  })