/* Services */

angular.module('well.services', [])
  .factory('User', function ($q, $rootScope, $location, $timeout) {
    var user = this
    this.firebase = new Firebase('https://well.firebaseio.com/')
    
    this.info = this.firebase.getAuth()

    this.hashedEmail = email => 
      'wiaglif+' + CryptoJS.SHA3(email, { outputLength: 224 }).toString() + '@gmail.com'

    this.signup = (email, password) => {
      var deferred = $q.defer()

      this.email = email
      this.firebase.createUser({
        email    : email, // this.hashedEmail(email)
        password : password
      }, error => {
        if (error === null) {
          console.log('User created successfully')
          deferred.resolve()
        } else {
          console.log('Error creating user:', error)
          deferred.reject(error)
        }
      })

      return deferred.promise
    }

    this.login = (email, password) => {
      var deferred = $q.defer()

      this.email = email
      this.firebase.authWithPassword({
        email    : email, // this.hashedEmail(email)
        password : password
      }, (error, authData) => {
        if (error === null) {
          // user authenticated with Firebase
          $timeout(function () {
            user.info = authData
          })
          console.log('User ID: ' + authData.uid + ', Provider: ' + authData.provider)
          deferred.resolve(authData)
        } else {
          console.log('Error authenticating user:', error)
          deferred.reject(error)
        }
      })

      return deferred.promise
    }

    this.firebase.onAuth(auth => {
      $timeout(() => {
        $rootScope.$broadcast('auth')
        user.info = auth
      })
    })

    this.unauth = () => {
      this.firebase.unauth()
      this.info = null
    }

    this.setAspects = aspects => {
      this.firebase.onAuth(auth => {
        if (auth) user.firebase.child('aspects/' + auth.uid).set(aspects, () => user.aspects = aspects)
      })
    }

    this.getAspects = () => {
      var deferred = $q.defer()
      if (this.aspects) {
        deferred.resolve(this.aspects)
      } else {
        this.firebase.onAuth(auth => {
          if (auth) user.firebase.child('aspects/' + auth.uid)
            .on('value', snapshot => {
              user.aspects = snapshot.val()
              deferred.resolve(snapshot.val()) 
            })
        })
      }
      return deferred.promise
    }

    this.test = () => 'test'

    this.addData = (module, data) => {
      console.log(module, data)
      this.firebase.onAuth(auth => {
        console.log(auth)
        if (auth) this.firebase.child('module/' + module + '/' + this.info.uid).push(data)
      })
    }

    this.getData = (module, limit) => {
      var deferred = $q.defer()
      this.firebase.onAuth(auth => {
        if (auth) this.firebase.child('module/' + module + '/' + this.info.uid)
          .on('value', (snapshot) => deferred.resolve(snapshot.val()) )
      })
      return deferred.promise
    }

    return this
  })
  .factory('Aspects', function (User, $q, $location) {
    this.texts = {
      'Family Well-Being': 'the overall well-being of you and your family',
      'Family Happiness': 'your and your familyâ€™s happiness',
      'Health': 'your health (physical and mental)',
      'Morality': 'your morality (that is, living up to your personal values)',
      'Family Relationships': 'your family relationships',
      'Financial Security': 'your financial security',
      'Options & Possibilities': 'your options and possibilities in life',
      'Meaning & Value': 'your sense that your life is meaningful and has value',
      'Life Satisfaction': 'how satisfied you are with your life',
      'Enough Time & Money': 'your feeling that you have enough time and money',
      'Peace, Calm & Harmony': 'how peaceful, calm, and harmonious your life is',
      'Independence': 'your feeling of independence and self-sufficiency',
      'Making a Difference': 'your sense that you are making a difference in the world',
      'Worthwileness': 'your feeling that the things you do are worthwhile',
      'Safety & Security': 'your physical safety and security',
      'Respect': 'your being treated with dignity and respect by others',
      'Amount of Day Happy': 'how much of the time you feel happy',
      'Improvement': 'your sense that things are getting better and better',
      'Overall Happiness': 'how happy you are overall with your life',
      'Life Longevity': 'your chance to live a long life'
    }

    var five = _.sample(Object.keys(this.texts), 5)
    this.names = () => {
      var deferred = $q.defer()
      User.getAspects().then(aspects => {
        var as
        if ($location.path().split('/')[1].indexOf('overlap') !== -1) {
          as = aspects.slice(0,3)
        } else {
          as = aspects
        }
        if (aspects) {
          deferred.resolve(as)
        } else {
          User.setAspects(five)
          deferred.resolve(as)
        }
      })
      return deferred.promise
    }

    this.getColor = (aspect, level) => {
      if (User.aspects) {
        var h = (User.aspects.indexOf(aspect) + 6) * 70,
          l =  .99 + (level / 100) * -.24
        return d3.hsl(h, .8, l).toString()
      }
    }

    return this
  })
  .factory('Style', function ($window) {
    var sheet = (function() {
      var style = document.createElement('style')

      // Add a media (and/or media query) here if you'd like!
      // style.setAttribute("media", "screen")
      // style.setAttribute("media", "@media only screen and (max-width : 1024px)")

      // WebKit hack :(
      style.appendChild(document.createTextNode(''))

      document.head.appendChild(style)

      return style.sheet
    })()

    return function (selector, rules, index) {
      if (sheet.insertRule) {
        sheet.insertRule(selector + '{' + rules + '}', index);
      } else {
        sheet.addRule(selector, rules, index);
      }
    }
  })
  .factory('Slide', function () {
    return function (element, duration) {
      var neu = d3.select(element).style('opacity', '0')
      var w = neu.style('width')

      var old = neu.select(function() { return this.parentNode.insertBefore(this.cloneNode(true), this) })
          .style('opacity', '1')

      var parent = neu.select(function () { return this.parentNode })

      parent.style('pointer-events', 'none')

      old
        .transition()
          .duration(duration)
          .style('left', '-' + w)
          .style('opacity', '0')
          .each('end', function () {
            old.remove()
            parent.style('pointer-events', 'auto')
          })

      neu
        .transition()
          .duration(duration)
          .style('left', '-' + w)
          .style('opacity', '1')
          .each('end', function () {
            neu.style('left', '0px')
          })
    }
  })