angular.module('well.overlap', [])
  .factory('Overlap', function (User, Aspects, Survey) {
    var overlap = this
    this.doubled = Math.random() > 0.5
    this.preamble = Math.random() > 0.5
    this.decisions = []
    this.skipped = []

    this.record = (decision) => {
      this.decisions.push(decision)
    }

    this.submit = () => {
      console.log('submit')
      Aspects.names().then(aspects => {
        Survey.getResponses().then(responses => {
          console.log(aspects, responses)
          User.addData('overlap', {
            date: Date.now(),
            aspects: aspects,
            responses: responses,
            decisions: overlap.decisions,
            skipped: overlap.skipped
          })
        })
      })
    }

    return overlap
  })
  .factory('Abc', function (Overlap, Choices, Tradeoff, Survey, Aspects, $q) {
    var responses
    var abc = {}
    abc.MRS = {}

    abc.positiveOK = function (a, b, c) {
      return a < 93 && b < 93 && c < 93
    }

    abc.negativeOK = function (a, b, c) {
      return a > 8 && b > 8 && c > 8
    }

    abc.getSign = function (a, b, c) {
      var sign = Tradeoff.sign
      if (sign === 'positive') {
        if (this.positiveOK(a, b, c)) {
          return 'positive'
        } else if (this.negativeOK(a, b, c)) {
          return 'negative'
        } else {
          return false
        }
      } else if (sign === 'negative') {
        if (this.negativeOK(a, b, c)) {
          return 'negative'
        } else if (this.positiveOK(a, b, c)) {
          return 'positive'
        } else {
          return false
        }
      }
    }

    abc.choose = function (i) {
      Overlap.record({
        aspects: abc.aspects,
        i: i,
        t: Date.now()
      })

      var sign = this.getSign(responses[abc.aspects[0]], responses[abc.aspects[1]], responses[abc.aspects[2]])
      // check corner case and if tradeoff value > 8, i.e. [-8, -1] or [8, 1]
      if (!sign || i > 6 || sign === 'negative' && i === 3 && Overlap.doubled || sign === 'positive' && i === 6 && Overlap.doubled) {
        return false
      } else if (sign === 'positive') {
        return {
          aspects: this.aspects,
          tradeoff: Tradeoff.positive_tradeoffs[i],
          i: i
        }
      } else if (sign === 'negative') {
        return {
          aspects: this.aspects,
          tradeoff: Tradeoff.negative_tradeoffs[i],
          i: i
        }
      }
    }

    abc.loadChoice = () => {
      var deferred = $q.defer()
      Aspects.names().then(aspects => {
        abc.aspects = [aspects[0], aspects[1], aspects[2]]
        _.each(abc.aspects, function (a) {
          abc.MRS[a] = {}
          _.each(abc.aspects, function (b) {
            var mrs = Choices.getMRS(a, b)
            // for testing set MRS to 0, i.e. 2^0
            abc.MRS[a][b] = mrs ? mrs : 0
          })
        })

        Survey.safeRandomize().then(res => {
          responses = res
          deferred.resolve(abc.choose(0))
        })
      })
      return deferred.promise
    }

    return abc
  })
  .factory('Abac', function (Aspects, Survey, Overlap, Tradeoff, Choices, $q) {
    var abac = this
    this.pair = null
    var aspects
    var responses
    if (!Survey.responded()) Survey.randomize()

    this.choose = (i) => {
      Overlap.record({
        aspects: abac.aspects,
        i: i,
        t: Date.now()
      })

      if (!i) {
        i = 0
        this.pair = this.pairs.shift()
      } else {
        Choices.record(this.pair, i)
      }

      if (i > 6) {
        i = 0
        if (this.pairs.length) {
          this.pair = this.pairs.shift()
        } else {
          return false
        }
      }

      var tradeoff = Tradeoff.values(responses[this.pair[0]], responses[this.pair[1]])
      
      return abac.choice = {
        aspects: this.pair,
        tradeoff: tradeoff[i],
        i: i
      }
    }

    this.loadChoice = () => {
      var deferred = $q.defer()
      Aspects.names().then(names => {
        aspects = names
        abac.aspects = [aspects[0], aspects[1], aspects[2]]

        Survey.getResponses().then(res => {
          responses = res
          abac.pairs = [
            [aspects[0], aspects[1]],
            [aspects[0], aspects[2]]
          ]

          abac.pairs = _.filter(abac.pairs, function (pair) {
            var skip = Tradeoff.values(responses[pair[0]], responses[pair[1]]) === 'skip'
            if (skip) Overlap.skipped.push(pair)
            return !skip
          })

          abac.numberOfQs = abac.pairs.length * 3
          deferred.resolve(abac.choose(0))
        })
      })
      return deferred.promise
    }

    return this
  })
  .factory('Fine', function (Aspects, Survey, Overlap, Abc, $q, User) {
    var fine = this
    this.tuple = []
    var aspects
    var responses
    this.done = {}
    this.decisions = []
    this.positive = null

    this.choose = i => {
      this.decisions.push({
        tradeoff: this.tradeoff ? this.tradeoff : null,
        choice: i,
        t: Date.now()
      })

      if (this.tradeoff) this.done[this.tradeoff.toString()] = true

      if (this.decisions.length > 16) {
        this.submit()
        return false
      } else {
        while (!this.tradeoff || this.done[this.tradeoff.toString()]) {
          // randomly choose {A vs. C}, { B vs. C} and {A&B vs. C}
          var tradeoff = this.positive === 'positive' ? _.sample([ [8,8,0], [8,0,8], [8,8,8] ]) : _.sample([ [-8,-8,0], [-8,0,-8], [-8,-8,-8] ])
          console.log(tradeoff)
          this.tradeoff = _.map(tradeoff, n => {
            console.log(n)
            return this.positive === 'positive' ? Math.ceil(Math.random() * n) : Math.floor(Math.random() * n)
          })
        }
        return {
          tradeoff: this.tradeoff 
        }
      }
    }

    this.loadChoice = () => {
      var deferred = $q.defer()
      Aspects.names().then(names => {
        Survey.safeRandomize().then(res => {
          aspects = names
          responses = res
          this.positive = Abc.getSign(responses[aspects[0]], responses[aspects[1]], responses[aspects[2]])
          console.log(this.positive)
          deferred.resolve(fine.choose(0))
        })
      })
      return deferred.promise
    }

    this.submit = () => {
      console.log('submit')
      Aspects.names().then(aspects => {
        Survey.getResponses().then(responses => {
          User.addData('fineoverlap', {
            date: Date.now(),
            aspects: aspects,
            responses: responses,
            decisions: this.decisions
          })
        })
      })
    }
    return fine
  })
  .controller('AbacCtrl', function ($scope, Choices, Abac, Slide, $location) {
    $scope.choice = {}
    $scope.choices = Choices

    Abac.loadChoice().then(choice => {
      $scope.choice = choice
    })

    $scope.choose = function (i) {
      $scope.choice = Abac.choose(i)
      Slide('.options', 300)
    }

    // the last choice returns false
    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / 9
      if (!c) {
        $location.path('overlap/abc')
      }
    })
  })
  .controller('AbcCtrl', function ($scope, Overlap, Abc, $location, Slide, Two, Abac, Choices) {
    $scope.overlap = Overlap
    $scope.abc = Abc
    $scope.choice = true
    if (Overlap.preamble) {
      Abc.loadChoice().then(choice => $scope.choice = choice)
    }

    $scope.hidePreamble = () => {
      Overlap.preamble = true
      setTimeout(() => {
        Abc.loadChoice().then(choice => $scope.choice = choice)
      }, 1)
    }

    $scope.choose = function (i) {
      $scope.choice = Abc.choose(i)
      Slide('.options', 300)
    }

    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / 9
      if (!c) {
        Overlap.submit()
        $location.path('overlap/exit')
      }
    })
  })
  .controller('FineCtrl', function ($scope, Overlap, Fine, $location, Slide, Two, Abac, Choices) {
    $scope.overlap = Overlap
    $scope.choice = true
    Fine.loadChoice().then(choice => {
      $scope.choice = choice
    })

    $scope.hidePreamble = () => {
      Overlap.preamble = true
      setTimeout(() => {
        Fine.loadChoice().then(choice => $scope.choice = choice)
      }, 1)
    }

    $scope.choose = function (i) {
      $scope.choice = Fine.choose(i)
      Slide('.options', 300)
    }

    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / 12
      if (!c) {
        Fine.submit()
        $location.path('fineoverlap/exit')
      }
    })
  })