/* Directives */

angular.module('well.directives', [])
  .directive('earth', function ($window, Aspects) {
    return {
      restrict: 'EA',
      scope: {},
      link: function (scope, element, attrs) {
        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        var splash = d3.select('.splash')

        scope.render = function () {
          splash.selectAll('.earth *').remove()

          var width = window.innerWidth,
              height = window.innerHeight

          var projection = d3.geo.orthographic()
              .precision(0)
              .scale(500)
              .translate([width / 2, height / 2])
              .clipAngle(180)

          var canvas = d3.select('.splash .earth').append('canvas')
              .attr('width', width)
              .attr('height', height)

          var context = canvas.node().getContext('2d')

          var path = d3.geo.path()
              .projection(projection)
              .context(context)

          var delta = 0
          var offset = 0
          var ln = d3.random.logNormal(1)
          _.each(scope.responses, function (resp) {
            resp.rand = Math.random() * 20 + 10
          })

          d3.json('world-110m.json', function(error, world) {
            var x, y = 0
            window.onscroll = function () {
              y = (window.pageYOffset || document.scrollTop)  - (document.clientTop || 0) || 0
              rotate()
            }

            d3.select('body').on('mousemove', function () {
              x = d3.mouse(this)[0]
              rotate()
            })

            var land = topojson.feature(world, world.objects.land)
            var countries = topojson.feature(world, world.objects.countries).features

            _.each(countries, function (country) {
              country.properties.color = Math.random()
            })
            rotate()

            function rotate () {
              projection.rotate([x * .1, y * .1, 30])
              context.clearRect(0, 0, width, height)

              _.each(countries, function (country) {
                context.beginPath()
                context.strokeStyle = d3.hsl(country.properties.color * x, 1, .8).toString()
                path(country)
                context.stroke()
              })
            }
          })
        }
      }
    }
  })
  .directive('range', function($window, $timeout, Aspects) {
    return {
      restrict: 'EA',
      scope: {
        level: '='
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0])
          .append('svg')
          .style('width', '100%')
          .style('height', 22)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        var lastLevel = null
        scope.$watch('level', function (level) {
          if (level !== lastLevel) {
            scope.render()
          }
        })

        scope.render = function () {
          svg.selectAll('*').remove()

          var width = d3.select(element[0])[0][0].offsetWidth
          var height = 22

          var x = d3.scale.linear()
              .domain([0, 100])
              .range([6, width - 6])
              .clamp(true)

          var brush = d3.svg.brush()
              .x(x)
              .extent([scope.level, scope.level])
              .on('brush', brushed)

          var axis = svg.append('g')
              .attr('class', 'axis')

          axis.call(d3.svg.axis()
              .scale(x)
              .orient('bottom')
              .tickSize(0)
              .tickValues([]))
          
          var domain = axis.select('.domain')
              .attr('transform', 'translate(0, 10)')

          domain.select(function() { return this.parentNode.appendChild(this.cloneNode(true)); })
              .attr('class', 'halo')

          var slider = svg.append('g')
              .attr('class', 'slider')
              .call(brush)

          slider.selectAll('.extent,.resize')
              .remove()

          slider.select('.background')
              .attr('height', height)

          var pointy = function () {
            var cx = x(1),
              cy = 10
            return {
              set: function (pt) {
                cx = pt
                return [
                  { x: cx - 6, y: cy - 6 },
                  { x: cx + 6, y: cy - 6 },
                  { x: cx + 6, y: cy + 6 },
                  { x: cx, y: cy + 10 },
                  { x: cx - 6, y: cy + 6 }
                ]
              }

            }
          }

          var line = d3.svg.line()
            .x(function (d) { return d.x })
            .y(function (d) { return d.y })

          var handle = slider.append('path')
              .attr('class', 'handle')
              .attr('d', line(pointy().set(x(scope.level))) + 'Z')
              .style('display', 'none')

          // slider
          //     .call(brush.event)

          function brushed() {
            var value = brush.extent()[0]

            var v = x.invert(d3.mouse(this)[0])
            value = isNaN(v) ? value : v
            brush.extent([value, value])

            if (value) value = Math.round(value)

            handle
              .style('display', 'block')
              .attr('d', line( pointy().set( x(value) ) ) + 'Z')
            
            lastLevel = value
            $timeout(function () {
              scope.level = value
            })
          }
        }
      }
    }
  })
  .directive('bar', function($window, Aspects, $filter) {
    return {
      restrict: 'E',
      scope: {
        level: '=',
        diff: '=',
        name: '=',
        choice: '='
      },
      link: function(scope, element, attrs) {
        var svg = d3.select(element[0])
          .append('svg')
          .style('width', '100%')
          .style('height', 40)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch('level', function () {
          scope.render()
        })

        scope.$watch('diff', function (diff) {
          scope.render()
        })

        scope.$watch('name', function () {
          scope.render()
        })

        scope.$watch('choice', function () {
          scope.render()
        }, true)

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        var d = 0
        var diffVal = 0

        scope.render = function () {
          svg.selectAll('*').remove()

          var w = d3.select(element[0])[0][0].offsetWidth
          var width = w > 0 ? w : 100
          var height = 40

          var x = d3.scale.linear()
              .domain([0, 100])
              .range([6, width - 6])

          var axis = svg.append('g')
              .attr('class', 'axis')
              .attr('transform', 'translate(0,' + (height - 20) + ')')

          axis.call(d3.svg.axis()
              .scale(x)
              .orient('bottom')
              .tickSize(0)
              .tickPadding(12))

          axis.select('.domain').remove()

          var barBack = svg.append('rect')
              .attr('x', x(0))
              .attr('y', 4)
              .attr('height', 20)
              .attr('width', x(100) - x(0))
              .style('fill', Aspects.getColor(scope.name, -1))

          var bar = svg.append('rect')
              .attr('x', x(0))
              .attr('y', 4)
              .attr('height', 20)
              .attr('width', x(scope.level) - x(0))
              .style('fill', Aspects.getColor(scope.name, 100))

          var diff = svg.append('rect')
              .attr('y', 4)
              .attr('height', 20)
              .style('fill', Aspects.getColor(scope.name, 205))

          var rightArrow = (start, end) => {
            return 'M' + x(start) + ' 3 ' + 'L' + Number(x(end) - 4) + ' 3' +
              'L' + Number(x(end) - 4) + ' 1 ' + 'L' + x(end) + ' 4' + 'L' + Number(x(end) - 4) + ' 7' +
              'L' + Number(x(end) - 4) + ' 5' + 'L' + x(start) + ' 5 Z'
          }

          var leftArrow = (start, end) => {
            return 'M' + x(start) + ' 3 ' + 'L' + Number(x(end) + 4) + ' 3' +
              'L' + Number(x(end) + 4) + ' 1 ' + 'L' + x(end) + ' 4' + 'L' + Number(x(end) + 4) + ' 7' +
              'L' + Number(x(end) + 4) + ' 5' + 'L' + x(start) + ' 5 Z'
          }

          var arrow = svg.append('path')
                .attr('stroke', 'none')
                .style('fill', '#000')

          var text = svg.append('text')
              .attr('y', 14)
              .attr('dy', '.375em')
              .style('fill', '#000')
              .style('font-weight', '400')

          var level = svg.append('line')
                .attr('x1', x(scope.level + scope.diff))
                .attr('y1', 4)
                .attr('x2', x(scope.level + scope.diff))
                .attr('y2', 24)
                .attr('class', 'hidden')
                .style('stroke', '#000')

          if (scope.diff > 0) {
            diffVal = scope.diff
            d = x(scope.diff) - x(0)
            diff
              .attr('x', x(scope.level))
              .attr('width', 0)
              .transition()
              .delay(300)
              .duration(1000)
                .attr('width', d)
              .each('end', () => {
                level.attr('class', '')
              })

            arrow
              .attr('d', rightArrow(scope.level, scope.level))
              .transition()
              .delay(300)
              .duration(1000)
                .attr('d', rightArrow(scope.level, scope.level + scope.diff))

            text.text('+' + $filter('round1')(scope.diff))
            var w = text.style('width').replace('px','')

            if (x(100) - x(scope.level) - x(scope.diff) > w) {
              text
                .attr('x', x(scope.level) + x(scope.diff))
                .attr('dx',  4)
            } else {
              text
                .attr('x', x(100) - w) 
                .attr('dx',  -4)
            }
          } else if (scope.diff < 0) {
            diffVal = scope.diff
            d = x(-scope.diff) - x(0)
            diff
              .attr('x', x(scope.level))
              .attr('width', 0)
              .style('fill', Aspects.getColor(scope.name, 40))
              .transition()
              .delay(300)
              .duration(1000)
                .attr('x', x(scope.level) - d)
                .attr('width', d)
              .each('end', () => {
                level.attr('class', '')
              })

            arrow
              .attr('d', leftArrow(scope.level, scope.level))
              .transition()
              .delay(300)
              .duration(1000)
                .attr('d', leftArrow(scope.level, scope.level + scope.diff))

            text.text($filter('round1')(scope.diff))

            text
              .attr('x', x(scope.level) + x(scope.diff))
              .attr('dx',  4)
          } else if (diffVal > 0) {
            d = x(diffVal) - x(0)
            diff
              .attr('x', x(scope.level))
              .attr('width', d)
              .transition()
              .duration(1000)
                .attr('width', 0)
              .each('end', () => {
                level.attr('class', '')
              })

            // arrow
            //   .attr('d', leftArrow(scope.level + diffVal, scope.level))
            //   .transition()
            //   .delay(300)
            //   .duration(1000)
            //     .attr('d', leftArrow(scope.level, scope.level))
            //   .each('end', () => {
            //     arrow.remove()
            //   })
          } else if (diffVal < 0) {
            d = x(-diffVal) - x(0)
            diff
              .attr('x', x(scope.level) - d)
              .attr('width', d)
              .style('fill', Aspects.getColor(scope.name, 40))
              .transition()
              .delay(300)
              .duration(1000)
                .attr('x', x(scope.level))
                .attr('width', 0)
              .each('end', () => {
                level.attr('class', '')
              })

            // arrow
            //   .attr('d', rightArrow(scope.level + diffVal, scope.level))
            //   .transition()
            //   .delay(300)
            //   .duration(1000)
            //     .attr('d', rightArrow(scope.level, scope.level))
            //   .each('end', () => {
            //     arrow.remove()
            //   })
          }
        }
      }
    }
  })
  .directive('fix', function ($window) {
    return {
      restrict: 'EA',
      scope: {},
      link: function(scope, element, attrs) {
        var div = d3.select(element[0])
        var parent = d3.select(div[0][0].parentNode)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.render = function () {
          var bounds = parent[0][0].getBoundingClientRect()
          console.log(bounds, window.scrollLeft)
          div.style({
            'position': 'fixed',
            'padding': parent.style('padding'),
            'top': window.scrollTop ? bounds.top + window.scrollTop + 'px' : bounds.top + 'px',
            'left': window.scrollLeft ? bounds.left + window.scrollLeft +  'px' : bounds.left + 'px',
            'width': bounds.width + 'px'
          })
        }
      }
    }
  })
  .directive('prog', function ($window) {
    return {
      restrict: 'EA',
      scope: {
        complete: '='
      },
      link: function(scope, element, attrs) {
         var svg = d3.select(element[0])
          .append('svg')
          .style('width', '100%')
          .style('height', 8)

        window.onresize = function() {
          scope.$apply()
        }

        scope.$watch(function() {
          return angular.element($window)[0].innerWidth
        }, function() {
          scope.render()
        })

        scope.$watch('complete', function () {
          scope.render()
        })

        scope.render = function () {
          svg.selectAll('*').remove()
          var bounds = svg[0][0].getBoundingClientRect()
          svg.append('rect')
            .attr('height', 4)
            .attr('width', bounds.width * scope.complete)
            .attr('fill', '#559')
        }
      }
    }
  })

