angular.module('well.survey', [])
  .controller('ResultsCtrl', function ($scope, Aspects, Survey, Choices, User) {
    $scope.weights = Choices.weights()
    $scope.aspects.sorted = _.sortBy($scope.aspects.names, function (n) {
      return $scope.weights[n] * -1
    })

    $scope.data = User.getData('threetwo')
    
    // generate some dummy data for testing
    if (!$scope.weights) {
      $scope.weights = {}
      _.each($scope.aspects.names, function (a) {
        $scope.weights[a] = Math.random()
      })
      console.log($scope.weights)

      var sumW = 0
       _.each($scope.aspects.names, function (a) {
        sumW += $scope.weights[a]
      })

      _.each($scope.aspects.names, function (a) {
        $scope.weights[a] = $scope.weights[a] / sumW
      })
    }
  })
  .controller('SurveyCtrl', function ($scope, Aspects, Survey, Style, $location, $routeParams) {
    Survey.reset()
    Aspects.names().then(aspects => {
      $scope.aspects.names = aspects
      Survey.aspects = aspects
    })

    $scope.survey = {
      finished: false
    }

    $scope.$watch('responses', function () {
      if (Survey.responded()) {
        $scope.survey.finished = true
      }
    }, true)

    $scope.next = function () {
      switch ($location.path().split('/')[1]) {
        case 'threetwo':
          $location.path('threetwo/abbcca')
          break
        case 'overlap':
          $location.path('overlap/abac')
          break
        case 'fineoverlap':
          $location.path('fineoverlap/tradeoff')
          break
      }
    }
  })
  .factory('Survey', function (Aspects, $q) {
    var survey = this
    this.responses = {}

    this.getResponses = () => {
      var deferred = $q.defer()

      if (this.aspects) {
        deferred.resolve(survey.responses)
      } else {
        Aspects.names().then(aspects => {
          survey.aspects = aspects
          _.each(aspects, function (a) {
            if (!survey.responses[a]) survey.responses[a] = null
          })
          deferred.resolve(survey.responses)
        })
      }
      return deferred.promise
    }

    this.respond = (aspect, level) => this.responses[aspect] = level

    this.responded = () => this.aspects && _.every(survey.aspects, a => survey.responses[a])

    this.randomize = () => {
      console.log('randomized')
      var deferred = $q.defer()
      this.getResponses().then(responses => {
        Aspects.names().then(aspects => {
          for (var i in aspects) {
            survey.responses[aspects[i]] = Math.max( Math.ceil(Math.random() * 99), Math.max( Math.ceil(Math.random() * 99), Math.ceil(Math.random() * 99) ) )
          }

          deferred.resolve(survey.responses)
        })
      })
      return deferred.promise
    }

    this.safeRandomize = () => {
      if (this.responded()) { 
        return this.getResponses()
      } else {
        return this.randomize()
      }
    }

    this.reset = () => {
      if (this.aspects) _.each(survey.aspects, a => {
        survey.responses[a] = null
      })
      return this.responses
    }

    return this
  })