angular.module('well.tradeoff', [])
  //
  // --------------------------------------------------
  //
  //                  CONTROLLERS
  //
  // --------------------------------------------------
  //
  .controller('ThreeCtrl', function ($scope, Choices, Tradeoff, Three, Two, Slide, $location, $routeParams, $timeout) {
    $scope.choice = {}
    $scope.choices = Choices
    $scope.tradeoff = Tradeoff

    $scope.hidePreamble = () => {
      Tradeoff.preamble = false
      setTimeout(() => {
        Three.loadChoice().then(choice => $scope.choice = choice)
      }, 1)
    }

    if (!Tradeoff.preamble) Three.loadChoice().then(choice => {
      $scope.choice = choice
    })

    $scope.choose = function (i) {
      $scope.choice = Three.choose(i)
      Slide('.options', 300)
    }

    // the last choice returns false
    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / (Three.numberOfQs + Two.numberOfQs)
      if (!c) {
        $location.path('threetwo/dbec')
      }
    })
  })
  .controller('TwoCtrl', function ($scope, Choices, Two, Three, Slide, User, $location) {
    $scope.choice = true
    $scope.choices = Choices

    Two.loadChoice().then(choice => {
      $scope.choice = choice
    })

    $scope.choose = function (i) {
      $scope.choice = Two.choose(i)
      Slide('.options', 300)
    }

    // the last choice returns false
    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / (Three.numberOfQs + Two.numberOfQs)
      if (!c) {
        if (User.info.provider === 'password') {
          // user is authenticated with email and password
          Choices.submit()
          $location.path('threetwo/exit')
        } else {
          $location.path('threetwo/exit')
        }
      }
    })
  })
  .controller('TestCtrl', function ($scope, Choices, Two, Three, Slide, User, $location) {
    $scope.choice = true
    $scope.choices = Choices

    $scope.options = {
      one: 0,
      two: 0,
      choice: null,
      highlight: n => {
        if (!$scope.options.choice) {
          if (n === 1) {
            $scope.options.one = $scope.choice.tradeoff[0]
            $scope.options.two = 0
          } else {
            $scope.options.two = $scope.choice.tradeoff[1]
            $scope.options.one = 0
          }
        }
      },
      select: n => {
        $scope.options.choice = n
        if (n === 1) {
          $scope.options.one = $scope.choice.tradeoff[0]
          $scope.options.two = 0
        } else {
          $scope.options.two = $scope.choice.tradeoff[1]
          $scope.options.one = 0
        }
      },
      reset: () => {
        if (!$scope.options.choice) $scope.options.one = $scope.options.two = 0
      }
    }

    Two.loadChoice().then(choice => {
      $scope.choice = choice
    })

    $scope.choose = function () {
      var i = $scope.options.one ? $scope.choice.i *  2 + 1 : $scope.choice.i *  2 + 2
      $scope.choice = Two.choose(i)
      $scope.options.choice = null
      $scope.options.reset()
      Slide('.scenario', 300)
    }

    // the last choice returns false
    $scope.$watch('choice', function (c) {
      $scope.choices = Choices
      $scope.completed = Choices.step++ / Two.numberOfQs
      if (!c) {
        $location.path('threetwo/exit')
      }
    })

  })
  .controller('CompleteCtrl', function ($scope, User, Choices, $location) {
    $scope.login = {
      wrong: false,
      placeholder: {
        email: 'Enter your email',
        password: 'Password'
      },
      submit: function() {
        if (!$scope.login.email) {
          $scope.login.wrong = 'INVALID_EMAIL'
          $scope.login.placeholder.email = 'Enter a valid email'
        } else if (!$scope.login.password) {
          $scope.login.wrong = 'INVALID_PASSWORD'
          $scope.login.placeholder.email = 'Enter a valid password'
        } else {
          User.login(this.email, this.password).then(function(user) {
            $scope.login.wrong = 'in'
            console.log('Logged in as: ', user.uid)
            Choices.submit()
            $location.path('results')
          }, function(error) {
            $scope.login.wrong = error.code
            if (error.code === 'INVALID_EMAIL') {
              $scope.login.email = ''
              $scope.login.placeholder.email = 'Enter a valid email'
            } else if (error.code === 'INVALID_PASSWORD') {
              $scope.login.password = ''
              $scope.login.placeholder.password = 'Enter a valid password'
            }
            console.error('Login failed: ', error)
          })
        }
      }
    }
  })
  //
  // --------------------------------------------------
  //
  //                  SERVICES
  //
  // --------------------------------------------------
  //
  .factory('Tradeoff', function () {
    var tradeoff = {}
    // randomly decide whether to display positive or negative tradeoffs
    tradeoff.sign = Math.random() > 0.5 ? 'positive' : 'negative'
    console.log(tradeoff.sign)
    tradeoff.preamble = Math.random() > 0.5
    // 2 * i + 1 || 2 * i + 2
    // Each tuple represents the change in Option 1 for aspect X and the change in Option 2 for aspect Y respectively 
    tradeoff.positive_tradeoffs = [         
                                  [4,4],
                    [1,4],                   [4,1],
              [1,8],       [2,4],      [4,2],      [8,1],
      // MRS range based on final tradeoff where each number represents the MRS of Y in terms of X as a power of 2
      // <--- indicates relative pref for X   ---> indicates pref for Y
      [-4,-3],[-3,-2],[-2,-1],[-1,0],[0,1],[1,2],[2,3],[3,4]
    ]

    // The negative tradeoffs have the reverse mapping as the positive ones
    tradeoff.negative_tradeoffs = [         
                            [-4,-4],
              [-4,-1],                 [-1,-4],
          [-8,-1],    [-4,-2],    [-2,-4],   [-1,-8],
      [3,4],[2,3],[1,2],[0,1],[-1,0],[-2,-1],[-3,-2],[-4,-3]
    ]

    // where a and b are the rating levels for aspects a and b
    tradeoff.checkCorners = function (a, b) {
      if (a > 92 && b < 8) {
        return 'skip'
      } else if (a < 8 && b > 92) {
        return 'skip'
      } else if (a > 92 || b > 92) {
        return 'negative'
      } else if (a < 8 || b < 8) {
        return 'positive'
      } else {
        return null
      }
    }

    // where a and b are the rating levels for aspects a and b
    tradeoff.values = function (a, b) {
      var s = this.checkCorners(a, b) === null ? this.sign : this.checkCorners(a, b)
      if (s === 'positive') {
        return this.positive_tradeoffs
      } else if (s === 'negative') {
        return this.negative_tradeoffs
      } else {
        return 'skip'
      }
    }
    return tradeoff
  })
  // The Choices service records decisions and generates MRS values / weights
  .factory('Choices', function (Aspects, Survey, Tradeoff, User) {
    var aspects
    var choices = {
      MRS: {}
    }

    Aspects.names().then(names => {
      aspects = names
      _.each(aspects, function (aspect) {
        choices.MRS[aspect] = {}
      })
    })

    choices.step = 0
    choices.levels = Survey.responses
    choices.sign = Tradeoff.sign
    choices.skipped = []
    choices.decisions = []

    // add MRS value where d is the denomitar aspect, n the numerator and v the value as a power of 2
    choices.addMRS = function (n, d, range) {
      this.MRS[d][n] = (range[0] + range[1]) / 2
      this.MRS[n][d] = (range[0] + range[1]) / -2
    }

    // compute the MRS value of aspect n in terms of d, assuming one step of transitivity if necessary
    choices.getMRS = function (n, d) {
      if (d === n) {
        return 0
      } else if (this.MRS[d][n]) {
        return this.MRS[d][n]
      } else if (Object.keys(this.MRS[d]).length) {
        // check if there's an alternative route to n
        var alt = _.find(Object.keys(choices.MRS[d]), function (alt) {
          return choices.MRS[alt][n]
        })
        if (alt) {
          return choices.MRS[d][alt] + choices.MRS[alt][n]
        } else {
          return false
        }
      } else {
        return false
      }
    }

    // records each decision
    choices.record = function (pair, i) {
      this.decisions.push({
        aspects: pair,
        i: i,
        t: Date.now()
      })

      // if this is a final tradeoff
      if (i > 6) {
        // MRS of pair[1] in terms of pair[0]
        this.addMRS(pair[1], pair[0], Tradeoff.values(this.levels[pair[0]], this.levels[pair[1]])[i])
      }
    }

    choices.weights = function () {
      var sum = 0
      var ws = _.map(aspects, function (a) {
        var mrs = choices.getMRS(a, aspects[0])
        sum += Math.pow(2, mrs)
        return Math.pow(2, mrs)
      })

      var weights = {}
      _.each(aspects, function (aspect, i) {
        weights[aspect] = ws[i] / sum
      })
      return weights
    }

    choices.submit = function () {
      User.addData('threetwo', {
        date: Date.now(),
        aspects: aspects,
        responses: choices.levels,
        decisions: choices.decisions,
        skipped: choices.skipped,
        MRS: choices.MRS,
        weights: choices.weights()
      })
    }

    return choices
  })
  .factory('Three', function (Aspects, Survey, Tradeoff, Choices, $q) {
    var three = this
    this.pair = null
    this.numberOfQs = 9
    var aspects
    var responses

    this.choose = (i) => {
      console.log(responses)
      if (!i) {
        i = 0
        this.pair = this.pairs.shift()
      } else {
        Choices.record(this.pair, i)
      }

      if (i > 6) {
        i = 0
        if (this.pairs.length) {
          this.pair = this.pairs.shift()
        } else {
          return false
        }
      }

      var tradeoff = Tradeoff.values(responses[this.pair[0]], responses[this.pair[1]])

      return three.choice = {
        aspects: this.pair,
        tradeoff: tradeoff[i],
        i: i
      }
    }

    this.loadChoice = () => {
      var deferred = $q.defer()
      Aspects.names().then(names => {
        aspects = names
        three.aspects = [aspects[0], aspects[1], aspects[2]]

        Survey.safeRandomize().then(res => {
          responses = res
          three.pairs = _.map(three.aspects, function (aspect, i) {
            return [three.aspects[i], three.aspects[(i + 1) % 3]]
          })

          three.pairs = _.filter(three.pairs, function (pair) {
            var skip = Tradeoff.values(responses[pair[0]], responses[pair[1]]) === 'skip'
            if (skip) Choices.skipped.push(pair)
            return !skip
          })

          three.numberOfQs = three.pairs.length * 3
          console.log(res, responses)
          deferred.resolve(three.choose(0))
        })
      })
      return deferred.promise
    }

    return this
  })
  .factory('Two', function (Aspects, Survey, Tradeoff, Choices, Three, $q) {
    var aspects
    var responses
    if (!Survey.responded()) Survey.randomize()
    var two = this
    this.pair = null

    this.numberOfQs = 6

    this.choose = (i) => {
      if (!i) {
        i = 0
        this.pair = this.pairs.shift()
      } else {
        Choices.record(this.pair, i)
      }

      if (i > 6) {
        i = 0
        if (this.pairs.length) {
          this.pair = this.pairs.shift()
        } else {
          return false
        }
      }

      var tradeoff = Tradeoff.values(responses[this.pair[0]], responses[this.pair[1]])
      return {
        aspects: this.pair,
        tradeoff: tradeoff[i],
        i: i
      }
    }

    this.loadChoice = () => {
      var deferred = $q.defer()
      Aspects.names().then(names => {
        aspects = names
        two.aspects = _.difference(aspects, Three.aspects)

        Survey.safeRandomize().then(res => {
          responses = res
          two.pairs = [ [aspects[3], aspects[1]], [aspects[4], aspects[2]] ] 
          two.pairs = _.filter(two.pairs, function (pair) {
            var skip = Tradeoff.values(responses[pair[0]], responses[pair[1]]) === 'skip'
            if (skip) Choices.skipped.push(pair)
            return !skip
          })

          two.numberOfQs = two.pairs.length * 3
          deferred.resolve(two.choose())
        })
      })

      return deferred.promise
    }

    return this
  })